import React from 'react';
import { FluentProvider } from '@fluentui/react-components';
import { planktonTheme } from '../planktonTheme';
import Layout from "../components/layout";
import * as styles from './thank-you-for-request.module.css';
import Box1 from "../images/4.png";
import {Image} from "@fluentui/react-components";
import {useTranslation} from 'gatsby-plugin-react-i18next';
import { graphql } from 'gatsby';


const ThankYouForRequest: React.FC = () => {
  const { t } = useTranslation();
    return (
        <Layout>
            <FluentProvider theme={planktonTheme}>
                <div className={styles.container}>
                    <Image src={Box1} alt={t('Thank You for Your Request!')} className={styles.image}/>
                    <h1 className={styles.header}>{t('Thank You for Your Request!')}</h1>
                    <p className={styles.message}>{t('We appreciate your interest in our services. Our team will review your request and get in touch with you as soon as possible. If you have any urgent inquiries, feel free to contact us directly.')}</p>
                </div>
            </FluentProvider>
        </Layout>
    );
};

export default ThankYouForRequest;
export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;